import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../services/auth.service';
import { FormControl, Validators } from '@angular/forms';
@Component({
  selector: 'nx-profile',
  template: require('./profile.component.html'),
  styles: [require('./profile.component.css')]
})
export class ProfileComponent implements OnInit {
  @Output()
  logoutEvent: EventEmitter<any> = new EventEmitter<any>();

  @Input()
  nxId = 'nx-profile-component';

  auth: boolean = false;
  authService: AuthService;
  returnUrl: string;
  errMsg: string;
  username = new FormControl('');
  password = new FormControl('');
  isHide: boolean = true;
  showMenu: boolean = false;

  alias: string = '';


  constructor(
    private route: ActivatedRoute,
    private router: Router,
    _authService: AuthService
  ) {
    if (_authService.IsAuth()) {
      this.auth = true;
    }
    this.authService = _authService;
  }

  ngOnInit() {
    this.errMsg = null;
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';

    const uSer = this.authService.getUser();
    if (uSer) {
      // this.alias = uSer.username.substring(0, uSer.username.lastIndexOf('@'));
      // const a = this.alias.split('.');
      // this.alias = a[0].substring(0, 1);
      this.alias = uSer.nombre.substring(0, 2);
      this.alias = this.alias.toUpperCase();
    }
  }

  logOut(): void {
    this.logoutEvent.emit('logout');
    // this.authService.logOut().subscribe(
    //   result => {
    //     localStorage.removeItem('currentUser');
    //     this.router.navigateByUrl('/login');
    //   },
    //   error => {
    //     switch (error.status) {
    //       case 401:
    //       case 403:
    //         this.errMsg = 'Verifique usuario y password e intente de nuevo.';
    //         break;
    //       case 0:
    //       case 404:
    //         this.errMsg =
    //           'Error en la comunicación con el servidor, favor de intentar mas tarde.';
    //         break;
    //       default:
    //         this.errMsg = 'Ocurrió un error, favor de intentarlo mas tarde';
    //         break;
    //     }
    //   }
    // );
  }

  toggleMenu() {
    this.showMenu = !this.showMenu;
  }
}



