import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpHeaders, HttpClient, HttpResponse } from '@angular/common/http';
import { AuthModel } from '../models/auth.model';
import { CookieService } from 'ngx-cookie-service';


@Injectable()
export class AuthService {
  constructor(private http: HttpClient, private cookieService: CookieService) {
    // var currentUser = JSON.parse(localStorage.getItem('currentUser'));
  }

  public IsAuth(): boolean {
    // if (localStorage.getItem('currentUser')) {
    if (!this.cookieService.check('currentUser')) {
      if (localStorage.getItem('currentUser')) {
        return true;
      } else {
        return false;
      }
    } else {
      return true;
    }
  }

  public getToken(): string {
    const currentUser = localStorage.getItem('currentUser');
    // const currentUser = this.cookieService.get('currentUser');
    if (currentUser) {
      return JSON.parse(currentUser).token.token;
    } else {
      return '';
    }
  }

  public getUser(): AuthModel {
    const currentUser = localStorage.getItem('currentUser');
    // const currentUser = this.cookieService.get('currentUser');
    if (currentUser) {
      return JSON.parse(currentUser);
    } else {
      return null;
    }
  }
  public getUserId(): string {
    const currentUser = localStorage.getItem('currentUser');
    // const currentUser = this.cookieService.get('currentUser');
    if (currentUser) {
      return JSON.parse(currentUser).userId;
    } else {
      return null;
    }
  }

  public Authenticate(
    userId: string,
    password: string,
    application: string
  ): Observable<any> {
    const pass64 = btoa(password);
    const body = `{"username": "${userId}", "password": "${pass64}", "useCookie": false, "applicacion": "${application}"}`;
    const headers = new HttpHeaders().set('Content-Type', 'application/json');
    return this.http
      .post<AuthModel>('/seguridad/login', body.toString(), {
        headers: headers,
        withCredentials: true
      });
  }

  public logOut(): Observable<boolean> {
    const headers = new HttpHeaders().set('Content-Type', 'application/json');
    return this.http
      .delete<any>('/seguridad/login', {
        headers: headers,
        withCredentials: true
      });
  }
}



