import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Subject } from 'rxjs/Subject';
import { LoginMessage } from '../models/login.model';
// import { identifierModuleUrl } from '@angular/compiler';

@Injectable()
export class LoginService {
  private subject = new Subject<LoginMessage>();
  private keepAfterRouteChange = false;

  constructor() {}

  public gotError = false;
  public errorMessage = '';

  public errorHndlr() {
    return this.subject.asObservable();
  }

  setMessage(gotError: boolean, message = '') {
    this.message(gotError, message);
  }

  message(error: boolean, message: string, keepAfterRouteChange = false) {
    this.keepAfterRouteChange = keepAfterRouteChange;
    this.subject.next(<LoginMessage>{ gotError: error, message: message });
  }

  clear() {
    // clear message
    this.subject.next();
  }
}



