import { Injectable, Injector, Inject } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpErrorResponse
} from '@angular/common/http';
import { AuthService } from '../services/auth.service';
import { Observable, throwError } from 'rxjs';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import { Router } from '@angular/router';

import { LoadingService } from '../services/loading.service';
import { LoginService } from '../services/login.service';
import { catchError, tap, finalize } from 'rxjs/operators';
import { ErrorStreamService } from '../services/error-stream.service';
import { CookieService } from 'ngx-cookie-service';

@Injectable()
export class AuthInterceptor implements HttpInterceptor {
  private endpoint: string;

  private endsWith(base: string, suffix: string): boolean {
    return base.indexOf(suffix, base.length - suffix.length) >= 0;
  }

  constructor(
    @Inject('config') private config: any,
    private injector: Injector,
    private router: Router,
    private loadingService: LoadingService,
    private loginService: LoginService,
    private errorService: ErrorStreamService,
    private cookieService: CookieService
  ) {
    this.endpoint = this.config.endpoint;
    if (!this.endsWith(this.endpoint, '/')) {
      this.endpoint = this.endpoint + '/';
    }
  }

  public intercept(
    request: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    // Loading....
    // this.loadingService.showLoading(true);
    this.loginService.setMessage(false, '');

    const authService = this.injector.get(AuthService);

    if (this.endsWith(request.url, 'login') && request.method === 'POST') {
      this.loadingService.showLoading(true);
      request = request.clone({
        url: this.config.endpoint + request.url
      });
    } else if (request.url.indexOf('assets') > -1) {
      this.loadingService.showLoading(false);
      request = request.clone({
        url: request.url
      });
    } else if (request.url.indexOf('http') === 0 && request.url.indexOf('://') > 0) {
      this.loadingService.showLoading(true);
      request = request.clone({
        url: request.url
      });
    } else {
      this.loadingService.showLoading(true);
      const token = authService.getToken();
      request = request.clone({
        url: this.config.endpoint + request.url,
        setHeaders: {
          Authorization: `${token || ''}`
        }
      });
    }
    return next.handle(request).pipe(
      tap(event => {
        // this.loadingService.showLoading(false);
      }),
      catchError(error => {
        // There is an Error
        this.loadingService.showLoading(false);
        if (error instanceof HttpErrorResponse) {
          if (error.status === 401 || error.status === 403) {
            if (error.status === 403) {
              // Logout
              const token = authService.getToken();
              request = request.clone({
                url: this.config.endpoint + '/seguridad/login',
                method: 'DELETE',
                setHeaders: {
                  Authorization: `${token || ''}`
                }
              });

              const rtn = next.handle(request);
            }
            localStorage.removeItem('currentUser');
            this.cookieService.delete('currentUser');
            this.router.navigateByUrl('/login');
          }

          if (error.status >= 500) {
            this.errorService.setMessage(error.status, error.error.message);
          }

          this.loginService.setMessage(true, error.error.message);
        }
        console.warn(error);
        return throwError(error);
      }),
      finalize(() => {
        this.loadingService.showLoading(false);
      })
    );
  }
}



