import { Component, OnInit, Injectable, Inject, Input } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../services/auth.service';
import { FormControl, Validators } from '@angular/forms';

import { LoginService } from '../../services/login.service';
import { LoginMessage } from '../../models/login.model';
import { CookieService } from 'ngx-cookie-service';
@Component({
  selector: 'nx-login',
  template: require('./login.component.html'),
  styles: [require('./login.component.css')]
})
export class LoginComponent implements OnInit {
  @Input()
  nxId = 'nx-profile-component';

  auth: boolean = false;
  authService: AuthService;
  returnUrl: string;
  errMsg: string;
  username = new FormControl('');
  password = new FormControl('');
  isHide: boolean = true;
  title: string;
  usernamePlaceholder: string = 'Usuario';
  usernameLabel: string;
  errorMessage = new LoginMessage();

  constructor(
    @Inject('config') private config: any,
    private route: ActivatedRoute,
    private router: Router,
    _authService: AuthService,
    private loginService: LoginService,
    private cookieService: CookieService
  ) {
    if (_authService.IsAuth()) {
      this.auth = true;
    }
    this.authService = _authService;
  }

  ngOnInit() {
    this.errorMessage.message = '';
    this.errorMessage.gotError = false;

    this.errMsg = null;
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
    this.title = this.config.applicationTitle || 'Acceso a usuario';
    this.usernameLabel = this.config.usernameLabel || 'Correo';

    this.loginService.errorHndlr().subscribe((msg: LoginMessage) => {
      this.errorMessage.gotError = msg.gotError;
      this.errorMessage.message = msg.message;
    });
  }

  Authenticate(): void {
    this.authService
      .Authenticate(
        this.username.value,
        this.password.value,
        this.config.application
      )
      .subscribe(
        result => {
          if (result) {
            this.router.navigateByUrl(this.returnUrl);
            if (result.token) {
              const data: string = JSON.stringify(result);
              // console.log(data);

              localStorage.setItem('currentUser', data);
              this.cookieService.set('currentUser', data);
            }
          }
        },
        error => {
          switch (error.status) {
            case 401:
            case 403:
              this.errMsg = 'Verifique usuario y password e intente de nuevo.';
              break;
            case 0:
            case 404:
              this.errMsg =
                'Error en la comunicación con el servidor, favor de intentar mas tarde.';
              break;
            default:
              this.errMsg = 'Ocurrió un error, favor de intentarlo mas tarde';
              break;
          }
        }
      );
  }

  public showPassword() {
    this.isHide = !this.isHide;
  }
}



